import { contactService } from '../services/contactService';
import { useForm } from '../hooks/useForm';
import { useEffect, useRef, useState } from 'react';

export const ContactEdit = ({ match, history }) => {
	const inputRef = useRef(null);
	const [contact, handleChange, setContact] = useForm(null);

	useEffect(() => {
		const loadContact = async () => {
			try {
				const id = match.params.id;
				const contact = id
					? await contactService.getContactById(id)
					: contactService.getEmptyContact();
				setContact(contact);
			} catch (err) {
				console.log('Had issues', err);
			}
		};
		loadContact();
	}, [match.params.id]);

	const onSaveContact = async (ev) => {
		ev.preventDefault();
		await contactService.saveContact(contact);
		history.push('/contact');
	};

	if (!contact) return <div>Loading...</div>;
	return (
		<form className="contact-edit">
			<label htmlFor="name">Name</label>
			<input
				placeholder="Name"
				type="text"
				value={contact.name}
				id="name"
				name="name"
				ref={inputRef}
				onChange={handleChange}
			/>

			<label htmlFor="phone">Phone</label>
			<input
				placeholder="Phone"
				type="text"
				value={contact.phone}
				name="phone"
				id="phone"
				onChange={handleChange}
			/>

			<label htmlFor="email">Email</label>
			<input
				placeholder="Email"
				type="email"
				name="email"
				id="email"
				onChange={handleChange}
				value={contact.email}
			/>
			<button onClick={onSaveContact}>Save</button>
		</form>
	);
};
