import { Component } from 'react';
import { bitcoinService } from '../services/bitcoinService.js';
import { userService } from '../services/userService.js';

export class BitcoinApp extends Component {
	state = {
		user: null,
		rate: null,
	};

	componentDidMount() {
		this.loadUsers();
		this.loadRate();
	}

	loadUsers = async () => {
		const user = await userService.getUser();
		this.setState({ user });
	};

	loadRate = async () => {
		const rate = await bitcoinService.getRate();
		this.setState({ rate });
	};

	render() {
		const { user, rate } = this.state;
		if (!user || !rate) return <div> Loading...</div>;

		return (
			<div className="bitcoin-app-container">
				<header className="bitcoin-header-container">
					<h1>Welcome {user.name}</h1>
					<h2>You have {user.coins} coins</h2>
				</header>
				<main className="bitcoin-app-content">
					<h3 className="bitcoin-content-header">
						The current Bitcoin rate is : ${(1 / rate).toFixed()}
					</h3>
				</main>
			</div>
		);
	}
}
