import React from 'react';
import { useForm } from '../hooks/useForm';

const _ContactsFilter = ({ onChangeFilter }) => {
	const [filterBy, handleChange] = useForm(
		{
			term: '',
		},
		onChangeFilter
	);

	return (
		<section className="filter-container">
			<h1 className="filter-header">Filter</h1>
			<form className="contacts-filter">
				<label htmlFor="term">
					Search Contacts
					<input
						value={filterBy.term}
						type="text"
						name="term"
						id="term"
						onChange={handleChange}
					/>
				</label>
			</form>
		</section>
	);
};

export const ContactFilter = React.memo(_ContactsFilter);
