import { storageService } from './async-storage.service';

const USERS_KEY = 'usersDB';

export const userService = {
	getUser,
};

const gUser = {
	name: 'Ochoa Hyde',
	coins: 100,
	moves: [],
};

async function getUser() {
	let user = await storageService.query(USERS_KEY);
	if (!user || !user.length) {
		user = gUser;
		storageService.post(USERS_KEY, user);
	}
	return user[0];
}

