import React, { Component } from 'react';
import { Sparklines, SparklinesLine } from 'react-sparklines';
import { bitcoinService } from '../services/bitcoinService.js';

export class StatsPage extends Component {
	state = {
		marketPriceData: [],
		tradeVolData: [],
	};
	componentDidMount() {
		this.getMarketPrice();
		this.getTradeVol();
	}
	getMarketPrice = async () => {
		const marketPriceData = await bitcoinService.getMarketPrice();
		this.setState({ marketPriceData });
	};

	getTradeVol = async () => {
		const tradeVolData = await bitcoinService.getTradeVolume();
		this.setState({ tradeVolData });
	};

	render() {
		const { marketPriceData, tradeVolData } = this.state;
		if (!marketPriceData || !tradeVolData) return <div>Loading...</div>;
		return (
			<section className="stats-container">
				<h1 className="stats-header">Statistics Page</h1>
				<div className="market-price-chart">
					<h1>Market Price Chart</h1>
					<Sparklines
						data={marketPriceData.map((value) => [
							value.y.toFixed(),
						])}
						width={200}
						height={50}
						margin={5}
					>
						<SparklinesLine color="#bfbfce" />
					</Sparklines>
				</div>
				<div className="trade-vol-chart">
					<h1>Trade Volume Chart</h1>
					<Sparklines
						data={tradeVolData.map((value) => [value.y])}
						width={200}
						height={50}
						margin={5}
					>
						<SparklinesLine color="#bfbfce" />
					</Sparklines>
				</div>
			</section>
		);
	}
}
