import { Component } from 'react';
import { contactService } from '../services/contactService';
import { Link } from 'react-router-dom';

export class ContactDetailsPage extends Component {
	state = {
		contact: null,
	};

	componentDidMount() {
		this.loadContact();
	}

	loadContact = async () => {
		const id = this.props.match.params.id;
		const contact = await contactService.getContactById(id);
		this.setState({ contact });
	};

	goBack = () => {
		this.props.history.push('/contact');
	};

	deleteContact = async (ev, contactId) => {
		ev.stopPropagation();
		await contactService.deleteContact(contactId);
		this.goBack();
	};

	render() {
		const { contact } = this.state;
		if (!contact) return <div>Loading...</div>;
		return (
			<section className="contact-details-container">
				<button onClick={this.goBack} className="btn btn-go-back">
					Go Back
				</button>
				<div className="contact-details">
					<h1 className="contact-name">{contact.name}</h1>
					<p>{contact.email}</p>
					<p>{contact.phone}</p>
					<p>
						Lorem ipsum dolor sit amet consectetur adipisicing elit.
						Doloremque corporis ut maxime saepe ad, veritatis optio,
						dolor aut ipsam vel error cum recusandae similique
						nesciunt amet nisi excepturi dolorum fuga.
					</p>
					<div className="contact-btns">
						<Link
							className="btn-edit"
							to={'/contact/edit/' + contact._id}
						>
							Edit
						</Link>
						<button
							className="btn-remove"
							onClick={(ev) =>
								this.deleteContact(ev, contact._id)
							}
						>
							Delete
						</button>
					</div>
				</div>
			</section>
		);
	}
}
